import React, { Component } from 'react'
import map from 'ramda/src/map'
import pipe from 'ramda/src/pipe'
import toPairs from 'ramda/src/toPairs'
import firebase from 'firebase'

import { throttle } from '../shared'
import style from './styles'
import Gallery from '../Gallery'
import Item from '../Item'
import settingsIcon from '../cog.svg'
import loginIcon from '../enter.svg'
import logoutIcon from '../exit.svg'

const Wrapper = style.wrapper('div')
const Title = style.title('span')
const StyledGallery = style.gallery(Gallery)
const Settings = style.settings('img')
const Login = style.login('img')
const Logout = style.logout('img')

const renderItems = pipe(
  toPairs,
  map(([key, { name, url, description }]) => (
    <Item key={name} name={name} url={url} description={description} />
  ))
)

const initialState = { authenticated: false, itemsLoaded: false, items: {} }

class App extends Component {
  constructor (props) {
    super(props)
    this.state = initialState
  }

  componentWillMount () {
    const itemsRef = firebase.database().ref('addonGallery/items').orderByKey()

    itemsRef.on('value', snapshot => {
      this.setState({
        itemsLoaded: true,
        items: snapshot.val()
      })
    })
  }

  logIn () {
    const provider = new firebase.auth.GoogleAuthProvider()
    const login = firebase.auth().signInWithPopup(provider)
    login.then(() => {
      this.setState({
        authenticated: true
      })
    })
  }

  logOut () {
    const logout = firebase.auth().signOut()
    logout.then(() => {
      this.setState({
        authenticated: false
      })
    })
  }

  render () {
    return (
      <Wrapper>

        <Title>I am Ding</Title>

        <StyledGallery>
          {renderItems(this.state.items)}
        </StyledGallery>

        {this.state.authenticated
          ? <Logout
            src={logoutIcon}
            alt='Logout'
            onClick={throttle(this.logOut.bind(this), 1000)}
            />
          : <Login
            src={loginIcon}
            alt='Login'
            onClick={throttle(this.logIn.bind(this), 2000)}
            />}

        {this.state.authenticated &&
          <Settings src={settingsIcon} alt='Settings' />}

      </Wrapper>
    )
  }
}

export default App



