import map from 'ramda/src/map'
import {
  createStyleFn,
  dimensions,
  roundEdges,
  centerGridItems
} from '../shared'

const styles = {
  wrapper: {
    marginBottom: '4vh',
    display: 'grid',
    gridTemplateColumns: '1fr 1fr 1fr',
    gridAutoRows: '15rem'
  }
}

export default map(createStyleFn, styles)



