import map from 'ramda/src/map'
import {
  createStyleFn,
  dimensions,
  roundEdges,
  centerGridItems
} from '../shared'

const iconTemplate = {
  position: 'fixed',
  width: '2rem',
  right: '0.5rem',
  opacity: '0.3',
  ':hover': {
    opacity: '0.6'
  },
  ':active': {
    opacity: '0.8'
  }
}

const styles = {
  wrapper: {
    width: '100vw',
    height: '100vh',
    background: 'whitesmoke',
    display: 'grid',
    gridTemplateColumns: '10vh 1fr 10vh',
    gridTemplateRows: '4vh 10vh 3vh auto',
    gridTemplateAreas: `
      '. .       .'
      '. title   .'
      '. .       .'
      '. gallery .'
    `
  },
  title: {
    marginLeft: '1.5rem',
    fontSize: '4rem',
    gridArea: 'title'
  },
  gallery: {
    gridArea: 'gallery'
  },
  settings: {
    ...iconTemplate,
    top: '3rem',
    right: '0.7rem'
  },
  login: {
    ...iconTemplate,
    top: '0.5rem',
    right: '0.7rem'
  },
  logout: {
    ...iconTemplate,
    top: '0.5rem'
  }
}

export default map(createStyleFn, styles)



