import cxs from 'cxs/component'

export const createStyleFn = styles => component => cxs(component)(styles)

export const centerGridItems = option => {
  let cssProps = {
    alignItems: 'center',
    justifyItems: 'center'
  }
  if (option === 'block' || option === 'vertical') {
    cssProps = { alignItems: 'center' }
  }
  if (option === 'inline' || option === 'horizontal') {
    cssProps = { justifyItems: 'center' }
  }
  return cssProps
}

export const dimensions = (width, height = width) => ({ width, height })

export const roundEdges = radius => ({
  borderRadius: radius || '0.1rem'
})

export const debounce = (fn, delay) => {
  var timer = null
  return function () {
    var context = this, args = arguments
    clearTimeout(timer)
    timer = setTimeout(function () {
      fn.apply(context, args)
    }, delay)
  }
}

export const throttle = (func, limit) => {
  var inThrottle, lastFunc, lastRan
  return function () {
    var context = this, args = arguments
    if (!inThrottle) {
      func.apply(context, args)
      lastRan = Date.now()
      inThrottle = true
    } else {
      clearTimeout(lastFunc)
      lastFunc = setTimeout(function () {
        if (Date.now() - lastRan >= limit) {
          func.apply(context, args)
          lastRan = Date.now()
        }
      }, limit - (Date.now() - lastRan))
    }
  }
}



