import React from 'react'
import style from './styles'

const Wrapper = style.wrapper('div')
const Name = style.name('div')
const Description = style.description('div')

const Item = props => {
  const openUrl = e => {
    window.open(props.url)
  }

  return (
    <Wrapper onClick={openUrl}>
      <Name>{props.name}</Name>
      <Description>{props.description}</Description>
    </Wrapper>
  )
}

export default Item



