import map from 'ramda/src/map'
import {
  createStyleFn,
  dimensions,
  roundEdges,
  centerGridItems
} from '../shared'

const styles = {
  wrapper: props => ({
    ...roundEdges('0.4rem'),
    padding: '1.5rem',
    background: '#f5f5f5',
    display: 'flex',
    flexDirection: 'column',
    ':hover': {
      cursor: 'pointer',
      background: '#F5DC73'
    },
    ':active': {
      cursor: 'pointer',
      background: '#F7CB11'
    }
  }),
  name: {
    marginBottom: '1rem',
    fontSize: '1.9rem'
  },
  description: {}
}

export default map(createStyleFn, styles)



